.program {
    background-color: $dark-background-color;
    height: 2050px;
    padding-top: 50px;
    background-image: url(../images/programm/mobile/main_pic.png);
    background-repeat: no-repeat;
    background-position-x:  100%;
    background-position-y: 76.6%;
    background-size: auto 137px;
    position: relative;
    dl {
        margin: 0;
    }

    @media screen and(min-width: $tablet) {
        height: 1355px;
        background-image: url(../images/programm/tablet/main_pic.png);
        background-size: auto 280px;
        background-position-y: 76%;
        padding-top: 60px;
    }
    @media screen and(min-width: $desktop) {
        height: 1237px;
        padding-top: 120px;
        background-image: url(../images/programm/desctop/main_pic.png);
        background-size: 40%;
        background-position-x: 0%;
        background-position-y: 676px;
    }

}
.program-title {
    color: $light-background-color;
    margin-bottom: 43px;

    @media screen and(min-width: $tablet) {
        text-align: start;
        margin-bottom: 52px;
    }

    @media screen and(min-width: $desktop) {
        margin-bottom: 160px;
    }
}
.program-semester {
    @media screen and(max-width: 767px) {
        padding-left: 100px;
        margin-bottom: 50px;
    }

    @media screen and(min-width: $tablet) and (max-width: 1343px) {
    padding-left: 118px;
    margin-bottom: 189px;
    }

}
.semester-list {
    @media screen and(max-width: 1343px) {
        position: relative;  
    }
    &::before {
        position: absolute;
        content: "";
        display: block;
        background-color: white;
        width: 1px;
        height: 1282px;

        @media screen and(max-width: 1343px) {
            transform: translateX(-50%);
            left: -20px;
            top: 10px;
        }
        @media screen and(min-width: $tablet) {
            height: 705px;
            left: -50px;

        }
    }
    @media screen and(min-width: $desktop) {
        display: flex;
        &::before {
            height: 1px;
            width: 100%;
            left: 0;
            top: 279px;
        }
    }
}
.semester-item-list {
    
    &:not(:last-child) {
        @media screen and(max-width: 767px) {
            margin-bottom: 40px;
        }
    }

    @media screen and(min-width: $tablet) and (max-width: 1343px){
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }
    @media screen and(min-width: $desktop) {
        width: calc((100% - 30px * 4) / 5);
        &:not(:last-child) {
            margin-right: 30px;
        }
    }
}
.semester-title {
    margin-bottom: 11px;

    color: $light-background-color;
    font-weight: bold;
    font-size: 14px;
    line-height: 2;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    
    
    @media screen and(min-width: $desktop) {
            position: relative;
    }


    &::before {
        position: absolute;
        content: "";
        display: block;
        width: 12px;
        height: 12px;
        background-color: $hover-color;
        border-radius: 50%;

        

        @media screen and(max-width: 1343px) {
            transform: translate(-50%, 60%);
            left: -20px;
            
        }
        @media screen and(min-width: $tablet) {
            left: -50px;
        }
        @media screen and(min-width: $desktop) {
            width: 17.4px;
            height: 16px;
            left: 0px;
            top: -67px;
        }
    }
    @media screen and(min-width: $desktop) {
        font-size: 30px;
        line-height: 0.93;
        margin-bottom: 25px;
    }

}

.term {
    color: $hover-color;
    margin-bottom: 11px;
    font-weight: bold;
    font-size: 12px;
    line-height: 1.67;
    letter-spacing: 0.08em;

    @media screen and(min-width: $desktop) {
        font-size: 20px;
        line-height: 1.5;
        margin-bottom: 10px;
    }


}
.definition {

    margin-left: 0px;

    @media screen and(max-width: 767px) {
        margin-bottom: 15px;
    }
}
.semester {
    color: $secondary-text-color;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.67;
    letter-spacing: 0.08em;
    opacity: 0.5;

        @media screen and(min-width: $desktop) {
        font-size: 18px;
        line-height: 1.55;
    }
}

.semester-flex {
    @media screen and(min-width: $tablet){
        display: flex;
    }

    @media screen and(min-width: $desktop) {
        flex-direction: column;
    }

}

.semester-flex-item {
    @media screen and(min-width: $tablet) and (max-width: 1343px){
        &:first-child {
            margin-right: 50px;
            width: 176px;
        }
        &:nth-child(even) {
            width: 155px;  
            margin-right: 50px;          
        }

    }
    @media screen and(min-width: $desktop) {
        &:not(:last-child) {
            margin-bottom: 25px;
        }
    }

}

.program-advantages-title {
    color: $light-background-color;
    text-align: start;
    width: 180px;
    line-height: 1.35;
    margin-bottom: 30px;

        @media screen and(min-width: $tablet) {
            width: 100%;
            text-align: center;
        }
        @media screen and(min-width: $desktop) {
            opacity: 0;
        }
}
.advantages-modal {
    position: absolute;
    width: 100%;
    height: 638px;
    left: 50%;
    transform: translate(-50%, 0);
    padding-top: 53px;
    padding-right: 30px;
    padding-left: 30px;
    padding-bottom: 20px;
    counter-reset: section;

    background-color: $light-background-color;
    box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);


    @media screen and(min-width: $tablet) {
        height: 394px;
        padding-top: 63px;
        padding-right: 118px;
        padding-left: 98px;
        padding-bottom: 25px;
    }

    @media screen and(min-width: $desktop) {
        height: 398px;
        padding-top: 120px;
        padding-right: 82px;
        padding-left: 84px;
        padding-bottom: 70px;
        bottom: 0;
        top: -125px;
        transform: translate(-50%, 50%);

    }
}

.program-wraper {
    position: relative;
}

.advantages-modal-list {
    font-weight: bold;
    font-size: 14px;
    line-height: 1.43;
    letter-spacing: 0.08em;
    list-style-type: none;

    @media screen and(min-width: $tablet) {
        display: flex;
        flex-wrap: wrap;
    }
    @media screen and(min-width: $desktop) {
        font-size: 20px;
        line-height: 1.4;
    }
}
.advantages-list-item {
    position: relative;

    @media screen and(max-width: 767px) {
        &:not(:last-child) {
            margin-bottom: 53px;
        }
    }
    // &::before {
    //     counter-increment: section;
    //     content: "— 0" counter(section); 
    //     color: $hover-color;
    //     position: absolute;
    //     left: 0;
    //     top: -10px;
    //     transform: translate(0, -100%);

    //     font-weight: bold;
    //     font-size: 14px;
    //     line-height: 1.642;
    //     letter-spacing: 0.08em;

    //     @media screen and(min-width: $desktop) {
    //         font-size: 20px;
    //         line-height: 1.65;
    //     }
    // }

    @media screen and(min-width: $tablet) and (max-width: 1343px) {

        &:nth-last-child(n+3) {
            margin-bottom: 63px;
        }
        &:nth-of-type(odd) {
            width: calc((100% - 20px * 1) / 2);
            margin-right: 20px;
        }
        &:nth-of-type(even) {
            width: 216px;
        }
    }

    @media screen and(min-width: $desktop) {
    &:not(:nth-child(3n)) {
        margin-right: 40px;
    }
    &:not(:nth-last-child(-n+3)) {
        margin-bottom: 75px;
    }
    width: calc((100% - 40px * 2) / 3);

}
    
}

